@font-face {
  font-family: 'ubuntu-regular';
  src: local("Ubuntu Regular");
  src: url("/static/fonts/ubuntu-regular.ttf") format("truetype"); }

@font-face {
  font-family: 'ubuntu-medium';
  src: local("Ubuntu Medium");
  src: url("/static/fonts/ubuntu-medium.ttf") format("truetype"); }

.gg-chevron-left {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 22px;
  height: 22px;
  border: 2px solid transparent;
  border-radius: 100px; }
  .gg-chevron-left::after {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    width: 10px;
    height: 10px;
    border-bottom: 2px solid;
    border-left: 2px solid;
    transform: rotate(45deg);
    left: 6px;
    top: 4px; }

.gg-search {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 16px;
  height: 16px;
  border: 2px solid;
  border-radius: 100%;
  margin-left: -4px;
  margin-top: -4px; }
  .gg-search::after {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    border-radius: 3px;
    width: 2px;
    height: 8px;
    background: currentColor;
    transform: rotate(-45deg);
    top: 10px;
    left: 12px; }

.icon-left {
  position: absolute;
  display: block;
  top: 25px;
  left: 25px;
  width: 50px;
  height: 50px;
  cursor: pointer; }
  .icon-left:hover .gg-chevron-left {
    color: #FFF; }
  .icon-left .gg-chevron-left {
    color: #00A0E4;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1.5); }

@keyframes showDetail {
  0% {
    opacity: .5;
    transform: translateY(600px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

@keyframes backDrop {
  0% {
    opacity: 0;
    transform: translateY(-300px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

.detail {
  position: relative;
  margin-top: 30vh;
  height: 85vh;
  padding-bottom: 10px;
  background: #00A0E4;
  animation: backDrop .6s ease; }
  .detail section {
    display: block;
    position: absolute;
    top: -15vh;
    left: 50%;
    margin-left: -45%;
    margin-bottom: 25px;
    width: 90vw;
    min-height: 460px;
    background: #FFF;
    padding: 20px;
    border-radius: 10px;
    box-sizing: border-box;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
    animation: showDetail .4s ease; }
    .detail section > img {
      position: absolute;
      top: -75px;
      left: 50%;
      margin-left: -75px;
      border-radius: 150px;
      box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3); }
    .detail section h1 {
      margin-top: 75px;
      font: 1em 'ubuntu-medium', sans-serif;
      color: #00A0E4;
      text-transform: uppercase;
      text-align: center; }
    .detail section p {
      font: .9em 'ubuntu-regular', sans-serif;
      color: #262626; }
      .detail section p::first-letter {
        text-transform: uppercase; }
    .detail section dl {
      margin-top: 30px; }
      .detail section dl dt {
        float: left;
        margin-right: 15px; }
        .detail section dl dt img {
          width: 64px;
          height: 64px;
          border-radius: 64px;
          box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3); }
      .detail section dl dd {
        font: .9em 'ubuntu-regular', sans-serif;
        color: #262626; }
        .detail section dl dd small {
          display: block;
          font: .8em 'ubuntu-regular', sans-serif;
          text-transform: uppercase;
          color: #6E6E6E; }
        .detail section dl dd b {
          font: 1em 'ubuntu-medium', sans-serif;
          color: #00A0E4;
          text-transform: uppercase; }
        .detail section dl dd address {
          display: block;
          margin-top: 5px;
          font: .9em 'ubuntu-regular', sans-serif;
          text-transform: lowercase;
          color: #262626; }
