@font-face {
  font-family: 'ubuntu-regular';
  src: local("Ubuntu Regular");
  src: url("/static/fonts/ubuntu-regular.ttf") format("truetype"); }

@font-face {
  font-family: 'ubuntu-medium';
  src: local("Ubuntu Medium");
  src: url("/static/fonts/ubuntu-medium.ttf") format("truetype"); }

.gg-chevron-left {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 22px;
  height: 22px;
  border: 2px solid transparent;
  border-radius: 100px; }
  .gg-chevron-left::after {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    width: 10px;
    height: 10px;
    border-bottom: 2px solid;
    border-left: 2px solid;
    transform: rotate(45deg);
    left: 6px;
    top: 4px; }

.gg-search {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 16px;
  height: 16px;
  border: 2px solid;
  border-radius: 100%;
  margin-left: -4px;
  margin-top: -4px; }
  .gg-search::after {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    border-radius: 3px;
    width: 2px;
    height: 8px;
    background: currentColor;
    transform: rotate(-45deg);
    top: 10px;
    left: 12px; }

body {
  margin: 0;
  background: #FFD400; }

header {
  padding: 20px 20px 0; }
  header h1 {
    width: 100px;
    height: 56px;
    margin: 0 auto;
    background: url("/static/images/logo-petz-small.png") no-repeat;
    background-size: 100px;
    text-indent: -9000px; }
    @media screen and (min-width: 1024px) {
      header h1 {
        width: 150px;
        height: 105px;
        background: url("/static/images/logo-petz-large.png") no-repeat;
        background-size: 150px; } }
