@font-face {
  font-family: 'ubuntu-regular';
  src: local("Ubuntu Regular");
  src: url("/static/fonts/ubuntu-regular.ttf") format("truetype"); }

@font-face {
  font-family: 'ubuntu-medium';
  src: local("Ubuntu Medium");
  src: url("/static/fonts/ubuntu-medium.ttf") format("truetype"); }

.gg-chevron-left {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 22px;
  height: 22px;
  border: 2px solid transparent;
  border-radius: 100px; }
  .gg-chevron-left::after {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    width: 10px;
    height: 10px;
    border-bottom: 2px solid;
    border-left: 2px solid;
    transform: rotate(45deg);
    left: 6px;
    top: 4px; }

.gg-search {
  box-sizing: border-box;
  position: relative;
  display: block;
  transform: scale(var(--ggs, 1));
  width: 16px;
  height: 16px;
  border: 2px solid;
  border-radius: 100%;
  margin-left: -4px;
  margin-top: -4px; }
  .gg-search::after {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute;
    border-radius: 3px;
    width: 2px;
    height: 8px;
    background: currentColor;
    transform: rotate(-45deg);
    top: 10px;
    left: 12px; }

.icon-right {
  position: absolute;
  display: block;
  top: 25px;
  right: 25px;
  width: 50px;
  height: 50px;
  cursor: pointer; }
  .icon-right:hover .gg-search {
    color: #FFF; }
  .icon-right .gg-search {
    color: #00A0E4;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1.5); }

@keyframes showList {
  0% {
    opacity: 0;
    transform: translateY(100px); }
  100% {
    opacity: 1;
    transform: translateY(0); } }

.list li:nth-child(1) {
  animation: showList 0.3s ease; }

.list li:nth-child(2) {
  animation: showList 0.6s ease; }

.list li:nth-child(3) {
  animation: showList 0.9s ease; }

.list li:nth-child(4) {
  animation: showList 1.2s ease; }

.list li:nth-child(5) {
  animation: showList 1.5s ease; }

.list li:nth-child(6) {
  animation: showList 1.8s ease; }

.list li:nth-child(7) {
  animation: showList 2.1s ease; }

.list li:nth-child(8) {
  animation: showList 2.4s ease; }

.list li:nth-child(9) {
  animation: showList 2.7s ease; }

.list li:nth-child(10) {
  animation: showList 3s ease; }

.list {
  padding: 5px 0;
  background: #00A0E4; }
  .list li {
    list-style: none;
    transition: height .4s ease; }
    .list li.hide {
      height: 1px;
      overflow: hidden; }
    .list li.show {
      height: auto; }
    .list li input {
      margin: 20px 20px 0 20px;
      padding: 20px;
      border: 0;
      font: 1em 'ubuntu-medium', sans-serif;
      color: #00A0E4;
      text-transform: uppercase;
      box-sizing: border-box;
      border-radius: 10px;
      outline: none;
      width: calc(100% - 40px); }
    .list li a {
      display: block;
      position: relative;
      min-height: 100px;
      margin: 20px;
      list-style: none;
      background: #FFF;
      padding: 20px 20px 20px 140px;
      border-radius: 10px;
      box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
      text-decoration: none;
      transition: all .2s ease; }
      .list li a:hover {
        transform: translateY(-3px);
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.5); }
      .list li a:active {
        transform: translateY(0);
        box-shadow: none; }
    .list li h1 {
      font: 1em 'ubuntu-medium', sans-serif;
      color: #00A0E4;
      text-transform: uppercase; }
    .list li h2 {
      margin: 20px;
      font: 1.5em 'ubuntu-medium', sans-serif;
      color: #FFF;
      text-transform: uppercase;
      text-align: center; }
    .list li p {
      font: .9em 'ubuntu-regular', sans-serif;
      color: #262626; }
      .list li p::first-letter {
        text-transform: uppercase; }
    .list li img {
      position: absolute;
      top: 20px;
      left: 20px;
      width: 100px;
      height: 100px;
      border-radius: 100px; }

@media screen and (min-width: 1024px) {
  .list {
    padding: 5px 20px; }
    .list li input {
      margin: 20px 0 0 0;
      width: 100%; }
    .list li a {
      min-height: 150px;
      max-width: 1280px;
      margin: 20px auto;
      padding: 20px 20px 20px 190px; }
    .list li h1 {
      font: 1.25em 'ubuntu-medium', sans-serif; }
    .list li p {
      font: 1.15em 'ubuntu-regular', sans-serif; }
    .list li img {
      width: 150px;
      height: 150px;
      border-radius: 150px; } }
